/* custom select styles */
.jcf-select {
  width: 100%;
  height: 34px;
  overflow: hidden;
  min-width: 150px;
  position: relative;
  background: #fff;
  border-radius: 2px;
  border: 1px solid #cdcdcd;
  display: inline-block;
  vertical-align: top;
}

.jcf-select select {
  top: 0;
  left: 0;
  z-index: 1;
}

.jcf-select .jcf-select-text {
  color: #000;
  display: block;
  cursor: default;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin: 6px 20px 0 9px;
}

.jcf-select .jcf-select-opener {
  position: absolute;
  text-align: center;
  background: transparent;
  width: 26px;
  bottom: 0;
  right: 0;
  top: 0;
}

.jcf-select .jcf-select-opener:after {
  width: 0;
  height: 0;
  content: '';
  border-style: solid;
  border-width: 4px 4px 0;
  border-color: #000 transparent transparent;
  position: absolute;
  left: auto;
  top: 14px;
  right: 9px;
  bottom: auto;
}

body > .jcf-select-drop {
  position: absolute;
  margin: -1px 0 0;
  z-index: 9999;
}

.jcf-select {
  border-radius: 4px;
}

body > .jcf-select-drop.jcf-drop-flipped {
  margin: 1px 0 0;
}

.jcf-select .jcf-select-drop {
  position: absolute;
  margin-top: 0px;
  z-index: 9999;
  top: 100%;
  left: -1px;
  right: -1px;
}

.jcf-select .jcf-drop-flipped {
  bottom: 100%;
  top: auto;
}

.jcf-select-drop .jcf-select-drop-content {
  border: 1px solid #cdcdcd;
}

/* multiple select styles */
.jcf-list-box {
  overflow: hidden;
  display: inline-block;
  border: 1px solid #b8c3c9;
  min-width: 200px;
  margin: 0 15px;
}

/* select options styles */
.jcf-list {
  display: inline-block;
  vertical-align: top;
  position: relative;
  background: #fff;
  line-height: 18px;
  font-size: 14px;
  width: 100%;
}

.jcf-list .jcf-list-content {
  vertical-align: top;
  display: inline-block;
  overflow: auto;
  width: 100%;
}

.jcf-list ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.jcf-list ul li {
  overflow: hidden;
  display: block;
}

.jcf-list .jcf-overflow {
  overflow: auto;
}

.jcf-list .jcf-option {
  white-space: nowrap;
  overflow: hidden;
  cursor: default;
  display: block;
  padding: 5px 9px;
  color: #656565;
  height: 1%;
}

.jcf-list .jcf-disabled {
  background: #fff !important;
  color: #aaa !important;
}

.jcf-select-drop .jcf-hover,
.jcf-list-box .jcf-selected {
  background: #eab011;
  color: #fff;
}

.jcf-list .jcf-optgroup-caption {
  white-space: nowrap;
  font-weight: bold;
  display: block;
  padding: 5px 9px;
  cursor: default;
  color: #000;
}

.jcf-list .jcf-optgroup .jcf-option {
  padding-left: 30px;
}

/* custom checkbox styles */
.jcf-checkbox {
  width: 17px;
  float: left;
  height: 17px;
  overflow: hidden;
  cursor: default;
  background: #fff;
  border-radius: 3px;
  position: relative;
  border: 1px solid #c9c9c9;
}

.jcf-checkbox span {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: none;
  position: absolute;
}

.jcf-checkbox span:after {
  width: 12px;
  height: 9px;
  content: '';
  position: absolute;
  left: 7px;
  top: 7px;
  right: auto;
  bottom: auto;
  background: url(../../../images/sprite.png) no-repeat -29px -81px;
}

:root .jcf-checkbox span {
  margin: -4px 0 0 -5px;
}

.jcf-checkbox input[type="checkbox"] {
  position: absolute;
  width: 100%;
  height: 100%;
  border: 0;
  margin: 0;
  left: 0;
  top: 0;
}

.jcf-checkbox.jcf-checked span {
  display: block;
}

.filters__list:after, .suggested-plans footer:after, .s-suggestion:after, .routes-activity:after {
  content: '';
  display: table;
  clear: both;
}

.suggested-plans footer li, .days-list__instagram-item, .routes-activity__info-block header span,
.routes-activity__info-block header h1, .routes-activity__tabs .nav-pills > li img,
.routes-activity__tabs .nav-pills > li .link-info, .routes-activity__instagram-item {
