@font-face{
  font-family:"PT-Sans";
  font-style:normal;
  font-weight:400;
  src:url("../../fonts/PTSans/PTSans-Regular.ttf") format('truetype'),
      url("../../fonts/PTSans/PTSans-Regular.otf") format('opentype'),
      url("../../fonts/PTSans/PTSans-Regular.eot") format('embedded-opentype'),
      url("../../fonts/PTSans/PTSans-Regular.woff2") format('woff2'),
      url("../../fonts/PTSans/PTSans-Regular.woff") format('woff'),
      url("../../fonts/PTSans/PTSans-Regular.eot?#iefix") format('embedded-opentype');
}

