#myModalX .modal-dialog{
  width: 600px;
}
.in-image,
.in-image>a>img{
  width:145px;
  height:145px;
  margin: 0;
}

#the-image img{
  width:500px;
  margin-left:30px
}

.instafeed{
  margin: 10px 0;
  width: 100%;
}

.instafeed:before,
.instafeed:after{
  content:'';
  display: table;
}
.instafeed:after{
  clear: both;
}

@media(max-width:1378px){
  .in-image,
  .in-image>a>img{
    width:140px;
    height:140px
  }
}
@media(max-width:1149px){
  .in-image,
  .in-image>a>img{
    width:125px;
    height:125px
  }
}

@media(max-width:991px){
  .in-image:nth-child(17n){
    display:none!important
  }
  .in-image:nth-child(18n){
    display:none!important
  }
  .in-image {
    width: 25%;
    height: auto;
    padding: 0;
  }
  .in-image>a>img{
    width: 100%;
    height: auto;
  }
}
@media(max-width:767px){
  #the-image img{
    width:400px!important;
    margin-left:30px
  }
  .in-image:nth-child(17n){
    display:block!important
  }
  .in-image:nth-child(18n){
    display:block!important
  }

  #myModalX .modal-dialog{
    width: 470px;
    margin: 50px auto;
  }
}
@media(max-width:480px){
  #the-image img{
    width:250px!important;
    margin-left:0!important
  }
  .in-image {
    width: 33.333%;
    height: auto;
    padding: 0;
  }
  #myModalX .modal-dialog{
    width: 300px;
  }
}